<template>
  <div class="download-btn">
    <img
      v-if="platform === 'IOS'"
      class="download-btn-ios"
      alt="Download POP IM"
      :src="appStoreBadge"
      @click="handleDownloadApp()">
    <img
      v-else-if="platform === 'ANDROID'"
      class="download-btn-android"
      alt="Download POP IM"
      :src="androidDownloadBadge"
      @click="handleDownloadApp('android')">
    <div v-else class="download-btn-group">
      <img
        class="download-btn-group-item download-btn-group-ios"
        alt="Download POP IM"
        :class="isShowQr ? 'hide' : 'show'"
        :src="appStoreBadge"
        @click="handleDownloadApp('ios')">
      <img
        class="download-btn-group-item download-btn-group-android"
        alt="Download POP IM"
        :class="isShowQr ? 'hide' : 'show'"
        :src="androidDownloadBadge"
        @click="handleDownloadApp('android')">
      <a
        class="download-btn-group-item download-btn-group-item-qr"
        :class="isShowQr ? 'hide' : 'show'"
        @mouseover="handleToggleQR()"
        @mouseout="handleToggleQR()">
          <img
            class="download-btn-group-item download-btn-group-qr"
            alt="qrcode"
            :src="qrCode">
      </a>
    </div>
    <div
      v-if="isShowQr"
      class="popover">
        <qrcode :value="config.yyb || 'https://www.sundayhi.com'" />
    </div>
  </div>
</template>

<script>
import Qrcode from '@xkeshi/vue-qrcode';
import appStoreBadge from '@/assets/app-store-badge.png';
import androidDownloadBadge from '@/assets/android-download-badge.png';
import qrCode from '@/assets/QRcode.png';

export default {
  name: 'Button',

  props: {
    config: {
      type: Object,
      require: true
    },
    urlSchema: {
      type: Object,
      require: true
    },
    platform: {
      type: String,
      default: 'ANDROID'
    }
  },

  components: {
    Qrcode
  },

  data () {
    return {
      appStoreBadge,
      androidDownloadBadge,
      qrCode,
      isShowQr: false
    };
  },

  methods: {
    handleToggleQR () {
      this.isShowQr = !this.isShowQr;
    },
    handleDownloadApp (platform) {
      if (platform) {
        if (platform === 'ios') {
          window.location.href = this.config.appstore;
        }
        if (platform === 'android') {
          window.location.href = this.config.android;
        }
      } else {
        const url = this.urlSchema.getDownloadUrl();
        window.location.href = url;
      }
    }
  }
};
</script>

