<template>
  <div class="download">
    <div v-if="isWx" class="download-guaid">
      <p class="download-guaid-1">请按右上角 •••</p>
      <p class="download-guaid-2">选择在 {{ browser.isIOS ? 'Safari' : '浏览器' }} 中打开</p>
      <img v-if="isWx" class="download-guaid-img" :src="browser.isIOS ? safariImg : chromeImg" alt="browser guaid">
    </div>
    <div class="download-logo">
      <img src="@/assets/app-logo.png" alt="logo">
    </div>
    <div class="download-text">
      <p class="download-text-name">POP IM</p>
      <p class="download-text-desc">真正的朋友，真实的生活</p>
    </div>
    <download-button
      v-if="!isWx"
      :platform="platform"
      :urlSchema="urlSchema"
      :config="config"></download-button>
    <footer v-if="platform === 'DESKTOP'" class="download-footer">
      <p>
        <a class="download-footer-privacy" target="_blank" href="https://sunday37.com/pop-privacy">隐私政策</a>
        <a class="download-footer-protocol" target="_blank" href="https://sunday37.com/pop-agreement">用户使用协议</a>
        <a class="download-footer-copyright">&copy;{{ curYear }} Sunday Morning Co.,Ltd</a>
        <a class="download-footer-num" target="_blank" href="http://gdcainfo.miitbeian.gov.cn">粤ICP备18070086号-3</a>
      </p>
    </footer>
  </div>
</template>

<script>
import chromeImg from '@/assets/Chrome.png';
import safariImg from '@/assets/Safari.jpg';
import browser from '@/utils/browser';
import DownloadButton from '../components/Button';
import UrlSchema from '@sundaymorning/urlschema';
import '@/utils/lib-flexible';

export default {
  name: 'Download',

  data () {
    return {
      browser,
      chromeImg,
      safariImg,
      curYear: this.getCurYear() || '2018',
      platform: 'ANDROID',
      isWx: true,
      config: {
        schema: '',
        appstore: 'https://itunes.apple.com/cn/app/pop-im/id1381210087?ls=1&mt=8',
        yyb: '',
        android: `https://dc-assets-normal-public.oss-cn-shenzhen.aliyuncs.com/pop-latest.apk?$timestamp=${new Date().getTime()}`
      },
      urlSchema: null
    };
  },

  components: {
    DownloadButton
  },

  created () {
    this.urlSchema = new UrlSchema(this.config);
  },

  mounted () {
    this.isWx = browser.isWx;
    this.handleSwitchPlatform();
  },

  methods: {
    getCurYear () {
      return new Date().getFullYear();
    },
    handleSwitchPlatform () {
      if (!browser.isMobile) {
        this.platform = 'DESKTOP';
      } else if (browser.isIOS || browser.isMac) {
        this.platform = 'IOS';
      } else {
        this.platform = 'ANDROID';
      }
    }
  }
};
</script>

